import React from "react";
import { MainContainer } from "components/container/MainContainer";

import {
  IconBedHospital,
  IconTooth,
  IconExamHospital,
  IconBrain,
  IconNurse,
  IconTeledicine,
  IconLifeInsurance,
  IconFuneral,
  IconLife,
  IconPharmacy,
  IconMedicalReport,
} from "assets/svg";
import Carousel from "react-multi-carousel";
import "./benefitsContainer.scss";

export const BenefitsContainer = () => {
  const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 4,
      slidesToSlide: 2, // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      slidesToSlide: 2, // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1, // optional, default to 1.
    },
  };

  return (
    <div className="benefits">
      <MainContainer>
        <div className="row text-center">
          <h2 className="fs-2 title">
            Conhea todos <span>nossos benefcios</span>
          </h2>
        </div>

        <Carousel
          swipeable
          ssr
          itemClass="image-item"
          responsive={responsive}
          removeArrowOnDeviceType={["tablet", "mobile"]}
          autoPlay
          autoPlaySpeed={6000}
          rewind
        >
          <div className="card">
            <div className="card-body">
              <IconTeledicine width="60px" height="60px" />
              <h5 className="card-title">Telemedicina</h5>
              <p className="card-text">
                Orientao mdica on-line 24 horas por dia, direto no seu
                celular, tablet ou computador.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconLifeInsurance width="60px" height="60px" />
              <h5 className="card-title">Seguro de Vida</h5>
              <p className="card-text">
                R$5.500,00 para um dos momentos mais delicados.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconFuneral width="60px" height="60px" />
              <h5 className="card-title">Auxlio Funeral</h5>
              <p className="card-text">
                Em caso de falecimento do beneficirio auxlio funerrio no
                valor de R$5.500,00. Alm de cesta bsica no valor de R$1.600,00
                pagos em 4 parcelas de R$400,00
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconLife width="60px" height="60px" />
              <h5 className="card-title">Seguro Acidentes Pessoais</h5>
              <p className="card-text">
                Seguro de acidentes pessoais no valor de R$5.500,00 aps
                confirmao de incapacidade e invalidez em exercer sua funo ou
                bito.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconPharmacy width="60px" height="60px" />
              <h5 className="card-title">Descontos em farmcias</h5>
              <p className="card-text">
                Conte com 2.000 lojas de farmcia com descontos de 5% a 30% em
                medicamentos e perfumaria.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconMedicalReport width="60px" height="60px" />
              <h5 className="card-title">Especialidades Mdicas</h5>
              <p className="card-text">
                Todas as especialidades mdicas para realizao de consultas
                presenciais e online.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconNurse width="60px" height="60px" />
              <h5 className="card-title">Anlises Clnicas</h5>
              <p className="card-text">
                Mais de 5 mil exames laboratoriais de anlises clnicas como
                hemograma, vitaminas, colesterol, HIV e outros.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconBedHospital width="60px" height="60px" />
              <h5 className="card-title">Procedimentos Ambulatoriais</h5>
              <p className="card-text">
                Procedimentos realizados em ambulatrio como exrese,
                histerossalpingografia, endoscopia, cirurgias e outros.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconTooth width="60px" height="60px" />
              <h5 className="card-title">Odontologia</h5>
              <p className="card-text">
                Cirurgies dentistas e especialistas. Tratamentos odontolgicos
                de baixa e alta complexidade, prteses, preventivos e estticos.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconExamHospital width="60px" height="60px" />
              <h5 className="card-title">Exames de Imagem</h5>
              <p className="card-text">
                Exames de imagem como ultrassom, tomografia, ressonncia,
                mamografia, Raio-X, densitometria ssea e outros.
              </p>
            </div>
          </div>

          <div className="card">
            <div className="card-body">
              <IconBrain width="60px" height="60px" />
              <h5 className="card-title">Psicologia</h5>
              <p className="card-text">
                Consultas e sesses de terapia com profissionais de diversas
                abordagens para o cuidado da sade mental.
              </p>
            </div>
          </div>
        </Carousel>
      </MainContainer>
    </div>
  );
};

export default BenefitsContainer;
